import { Component, ViewChild } from '@angular/core';
import { ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';



@IonicPage()
@Component({
  selector: 'page-calusacusbica-list',
  templateUrl: 'calusacusbica-list.html',
})
export class CalusacusbicaListPage {



  @ViewChild('valueBarsCanvas') valueBarsCanvas;
  valueBarsChart: any;

  @ViewChild('valueBarsCanvas2') valueBarsCanvas2;
  valueBarsChart2: any;


  titlexx: string = "";

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';

  public idtipo                 : any;
  public idtipo2                 : any;
  public idnombre                 : any;
  public idnombre2                 : any;
  public idcodigo2                 : any;
  ubica: any;
  fecha: any;
  nivelprx:any;
  idempresa:any;
  idafiliado:any='na';
  public hasFilter: boolean = false;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  idsuscriptor:any;
  idsuscriptortt:any;
  permisom: any=[];
  potros1: any=[];
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  pfiltro:boolean=false;
  tipox:any;
  jsonObject2 :any;
  rolxx:any;


  constructor( public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController,public storage: Storage
    ,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    public navParams: NavParams,     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })

    this.permisom = String(navParams.get("permiso")).split(",");
    this.potros1 = JSON.parse(JSON.stringify(navParams.get("potros1")))
    this.pingreso=(  this.permisom[0] == 'true');
    this.pconsulta=(  this.permisom[1] == 'true');
    this.peliminacion=(  this.permisom[2] == 'true');
    this.pcreacion=(  this.permisom[3] == 'true');
    this.pactualizacion=(  this.permisom[4] == 'true');
    this.pfiltro=(  this.permisom[5] == 'true');
    this.preporte=(  this.permisom[6] == 'true');

    var someJsonString = navParams.get("potros1");

    // jsonObject will contain a valid JavaScript object
  if(someJsonString!='')
  {

    this.jsonObject2 = JSON.parse(someJsonString);
  }

  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  randomScalingFactor = function() {
		return Math.ceil(Math.random() * 10.0) * Math.pow(10, Math.ceil(Math.random() * 5));
  };




  public dapermisos2(data,opcion){
    var re='';

    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos.ingreso + ',' + data[i].permisos.consulta+ ',' + data[i].permisos.elimina+ ',' + data[i].permisos.creacion+ ',' + data[i].permisos.actualiza+ ',' + data[i].permisos.filtro+ ',' + data[i].permisos.reporte
        break;

      }

    }

    return re;
  }

  public dapermisos(data,opcion){
    var re:any[];

    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos
        break;

      }

    }


  //DD/MM/YYYY hh:mm A
  return re;

 }


 public dapermisos_visible(opcion){
  var re:boolean=false;

  if(this.jsonObject2)
  {
  for (var i = 0; i < this.jsonObject2.length; i++)
  {
    if(this.jsonObject2[i].modulo==opcion)
    {
        re=!this.jsonObject2[i].permisos.ingreso
      break;

    }

  }
  }


//DD/MM/YYYY hh:mm A
return re;

}

showFilter()
  {


    var campo=''
    this.hasFilter = true;
    let select = {
      title: 'Seleccione profesor',
       inputs: [],
      buttons: [           {            text: 'Cancelar'     },    {       text: 'Buscar',handler: todo => {
          //buscamos
          campo=todo
          this.idsuscriptortt=campo
         var cad=''
          switch (this.nivelprx) {
            case '?key=1':////ubicaciones


            cad=campo+'/1/idiomasprofe'

            break;
            case '?key=11':////ubicaciones


            cad=campo+'/1/idiomasprofe'

            break;
            case '?key=2'://bimestre


            cad=campo+'/1/idiomasprofe2'

            break;
            case '?key=22'://bimestre


            cad=campo+'/1/idiomasprofe2'

            break;
          }




            let self = this;
            self.loading = true;


      this.regService.getReg(cad,'asignaubicacions').then((data) => {

        this.items=data;
        this.items2=data;
        self.loading = false;
    }, (err) => {
      this.msj.showError3('Error',err.error);
    });




        }  } ]


  }
  var filtro1=''
    var filtro2=''

  switch (this.nivelprx) {
    case '?key=1':////ubicaciones
    filtro1='1/2/asigna_profesor2'
    filtro2='asignacalusacs'

    break;
    case '?key=11':////ubicaciones
    filtro1='1/2/asigna_profesor2'
    filtro2='asignacalusacs'

    break;
    case '?key=2'://bimestre
      filtro1='1/2/asigna_profesor'
      filtro2='asignacalusacs'

    break;
    case '?key=22'://bimestre
    filtro1='1/2/asigna_profesor'
    filtro2='asignacalusacs'

  break;
  }


  this.regService.getReg(filtro1,filtro2).then((data) => {
    var aa:any;
    aa=data;

    for (var i = 0; i < aa.length; i++) {
      select.inputs.push({name: 'myInput', type: 'radio',label:aa[i].nombre,value:aa[i]._id});
  }
  let newAlert = this.alertCtrl.create(select);
  newAlert.present();
}, (err) => {
this.msj.showError3('Error',err.error);
});


  }


  pideregistros() {

    var cad=''
    switch (this.nivelprx) {
      case '?key=1':////ubicaciones
      this.rolxx=0
      this.tipox=''
      this.titlexx='Examenes de ubicaci贸n'
      cad=this.idsuscriptor+'/1/idiomasprofe'

      break;
      case '?key=11':////ubicaciones
      this.tipox=''
      this.rolxx=1
      this.titlexx='Actas de ubicaci贸n'
      cad=this.idsuscriptor+'/1/idiomasprofe'

      break;
      case '?key=2'://bimestre
        this.tipox='2'
        this.rolxx=0
      this.titlexx='Bimestre'
      cad=this.idsuscriptor+'/1/idiomasprofe2'

      break;
      case '?key=22'://bimestre
      this.tipox='2'
      this.rolxx=1
    this.titlexx='Listados'
    cad=this.idsuscriptor+'/1/idiomasprofe2'
    }


    if(this.tiposus[6]=='DOCENTE CALUSAC')
    {
      this.rolxx='23'
    }

      let self = this;
      self.loading = true;


if(this.rolxx=='23')
{
  this.regService.getReg(cad,'asignaubicacions').then((data) => {

    this.items=data;
    this.items2=data;
    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});

}
else{

  this.regService.getReg(cad,'asignaubicacions').then((data) => {

    this.items=data;
    this.items2=data;
    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});

}



  }


tiposus: any=[];

  ionViewDidLoad() {
    this.nivelprx = this.navParams.get("nivel");

    switch (this.nivelprx) {
      case '?key=1':////ubicaciones
      this.rolxx=0
      this.tipox=''
      this.titlexx='Examenes de ubicaci贸n'


      break;
      case '?key=11':////ubicaciones
      this.tipox=''
      this.rolxx=1
      this.titlexx='Examenes de ubicaci贸n'

      break;
      case '?key=2'://bimestre
        this.tipox='2'
        this.rolxx=0
      this.titlexx='Examenes de bimestre'

      break;
      case '?key=22'://bimestre
      this.tipox='2'
      this.rolxx=1
    this.titlexx='Examenes de bimestre'

    }




    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');


      this.idsuscriptor=this.tiposus[3]
      this.idsuscriptortt=this.idsuscriptor

      this.pideregistros();

    });




  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  salonRecord(item : any, no) : void
  {
    if(this.nivelprx==='?key=1')
    {
      this.navCtrl.push('DocenteNotas1ubicaPage', {sede:item.tipounidad,nombreidioma:item.nombre, titlexx:this.titlexx,codigoidioma: item.codigo,rolxx:this.rolxx,record : item ,tipo: this.tipox,idsuscriptor:this.idsuscriptortt,permiso:this.permisom,potros1:this.navParams.get("potros1")});

    }
    else
    {
    this.navCtrl.push('DocenteNotas1Page', {sede:item.tipounidad, nombreidioma:item.nombre,titlexx:this.titlexx,codigoidioma: item.codigo,rolxx:this.rolxx,record : item ,tipo: this.tipox,idsuscriptor:this.idsuscriptortt,permiso:this.permisom,potros1:this.navParams.get("potros1")});
  }
}




}



